// frontend/src/app/page.tsx
"use client";
import { useEffect, useMemo, useState } from "react";
import Navbar from "./components/Navbar";
import MultiSelectButton from "./components/MultiSelectButton";
import CategoryCard from "./components/CategoryCard";
import CustomQueryBox from "./components/CustomQueryBox";
import FinalPromptModal from "./components/FinalPromptModal";
import LoadingView from "./components/LoadingView";
import ResultView from "./components/ResultView";
import { fetchCatalog, sendQuery } from "./api/backend";

export default function HomePage() {
  const [catalog, setCatalog] = useState<any | null>(null);
  const [selected, setSelected] = useState<Record<string, boolean>>({});
  const [parsedFromCustom, setParsedFromCustom] = useState<any | null>(null);
  const [rowsPerCategory, setRowsPerCategory] = useState<Record<string, any[]>>({});
  const [showModal, setShowModal] = useState(false);
  const [finalPrompt, setFinalPrompt] = useState("");
  const [loading, setLoading] = useState(false);
  const [result, setResult] = useState<any | null>(null);

  useEffect(() => {
    fetchCatalog().then((c) => setCatalog(c));
  }, []);

  const MAIN = useMemo(() => [
    { id: "Road Sign", label: "Road Sign" },
    { id: "Road Marking", label: "Road Marking" },
    { id: "Traffic Calming Measures", label: "Traffic Calming Measures" },
    { id: "Custom Query", label: "Custom Query" },
  ], []);

  const toggle = (id: string) => {
    setSelected((s) => ({ ...s, [id]: !s[id] }));
    // initialize rows if turning on
    if (!selected[id]) {
      setRowsPerCategory((r) => ({ ...r, [id]: [{ problem: "", speed: "", notes: "" }] }));
    }
  };

  function updateRow(cat: string, idx: number, field: string, value: string) {
    setRowsPerCategory((prev) => {
      const arr = prev[cat] ? [...prev[cat]] : [];
      arr[idx] = { ...arr[idx], [field]: value };
      return { ...prev, [cat]: arr };
    });
  }

  function addRow(cat: string) {
    setRowsPerCategory((prev) => ({ ...prev, [cat]: [...(prev[cat] || []), { problem: "", speed: "", notes: "" }] }));
  }

  function removeRow(cat: string, idx: number) {
    setRowsPerCategory((prev) => ({ ...prev, [cat]: prev[cat].filter((_: any, i: number) => i !== idx) }));
  }

  function buildPrompt(): string {
    const parts: string[] = [];

    // structured entries
    for (const cat of Object.keys(selected)) {
      if (!selected[cat] || cat === "Custom Query") continue;
      const rows = rowsPerCategory[cat] || [];
      for (const r of rows) {
        const type = r.type || cat;
        const problem = r.problem || "";
        const speed = r.speed ? ` at ${r.speed}` : "";
        const notes = r.notes ? ` (${r.notes})` : "";
        parts.push(`${type} — ${problem}${speed}${notes}`);
      }
    }

    // custom parsed prompt
    if (parsedFromCustom?.prompt) {
      parts.push(parsedFromCustom.prompt);
    }

    return parts.join("; ");
  }

  const openReview = () => {
    const p = buildPrompt();
    setFinalPrompt(p);
    setShowModal(true);
  };

  const runAnalysis = async (promptText: string) => {
    setShowModal(false);
    setLoading(true);
    try {
      const res = await sendQuery(promptText, 4);
      setResult(res.data ?? res);
    } catch (e) {
      setResult({ answer: `Error: ${String(e)}` });
    } finally {
      setLoading(false);
    }
  };

  const restart = () => {
    setSelected({});
    setRowsPerCategory({});
    setParsedFromCustom(null);
    setFinalPrompt("");
    setResult(null);
  };

  return (
    <div className="min-h-screen bg-zinc-50 dark:bg-zinc-950 text-zinc-900 dark:text-zinc-100">
      <Navbar />
      <main className="max-w-6xl mx-auto p-6 space-y-6">
        <div className="rounded-lg p-6 bg-white dark:bg-zinc-900 shadow">
          <h1 className="text-2xl font-bold">Hi — how can I help you today?</h1>
          <p className="text-zinc-600 dark:text-zinc-400 mt-1">Select the query types (you can pick multiple). For custom queries, type directly.</p>

          <div className="mt-6 flex gap-4">
            {MAIN.map((m) => (
              <MultiSelectButton key={m.id} label={m.label} selected={!!selected[m.id]} onToggle={() => toggle(m.id)} />
            ))}
          </div>

          {/* Custom Query inline */}
          {selected["Custom Query"] && (
            <div className="mt-4">
              <CustomQueryBox onParsed={(p: any) => setParsedFromCustom(p)} />
            </div>
          )}
        </div>

        {/* Category Cards (one per selected non-custom) */}
        <div className="space-y-4">
          {Object.keys(selected).filter((k) => selected[k] && k !== "Custom Query").map((k) => (
            <div key={k}>
              <CategoryCard id={k} catalog={catalog} />
              {/* simple row controls per category (add row etc) can be added here */}
            </div>
          ))}
        </div>

        {/* Final controls */}
        <div className="flex gap-3">
          <button onClick={openReview} className="px-4 py-2 bg-blue-600 text-white rounded">Review Prompt</button>
          <button onClick={restart} className="px-4 py-2 border rounded">Restart</button>
        </div>

        {/* Loading / Result */}
        {loading && <LoadingView />}

        {result && <ResultView result={result} onRestart={restart} />}

        {showModal && (
          <FinalPromptModal
            initial={finalPrompt}
            onConfirm={(text) => runAnalysis(text)}
            onClose={() => setShowModal(false)}
          />
        )}
      </main>
    </div>
  );
}
